<template>
    <li class="hoverToBigger" v-for="(salItem,index) of parentItems">
      <div>
        <a>
          <div class="mainPic">
            <img :src="salItem.imgUrl" class="objectCover" alt="">
            <div class="goodDescription">
              <div>{{ salItem.title }}</div>
              <div class="priceIcon">
                <div class="price">
                  <div class="del">{{ salItem.price }}</div>
                  <div class="red">{{ salItem.salePrice }}</div>
                </div>
                <div class="icons">
                  <span  @click="toggleFavorite(salItem.id)">
                    <span class="favBtn" :class="{'isFavorite':isFavoriteArr.includes(salItem.id)} ">
                      <i class="fa-regular fa-heart loveIcon"></i>
                    </span>
                  </span>                  
                  <template v-if="isShow">
                    <span @click="removeCart(salItem)"><i class='fa-solid fa-trash trash'></i></span>
                  </template>  
                  <template v-else>
                    <span @click="addCart(salItem)"><i class="fa-solid fa-cart-shopping cart"></i></span>
                   </template>  
                </div>                    
              </div>              
            </div>
          </div>                  
        </a>
      </div>
    </li>
</template>

<script>
export default {
  name: 'saleItemComponent',
  props: ['parentItems', 'isShow'],
  data(){
    return{
    }
  },
  computed:{
    cart(){
      return this.$store.state.cart 
    },  
    isFavoriteArr(){
      return this.$store.state.isFavorite
    }
  },
  methods:{
    addCart(salItem){

      // //檢查是否有重複點擊shopcart，有重複數字就退出functuin
      for(let i = 0; i <= this.cart.length - 1; i++){
        if(this.cart[i].id === salItem.id){
          alert("已經加入購物車了")
          return false;
        }
      }   
      // 增加number到salitem Object
      salItem["number"] = 1
      salItem["totalPrice"] = salItem.salePrice
      this.$store.commit('addImg', salItem)
    },
    removeCart(salItem){
      for(let i = 0; i <= this.cart.length - 1; i++){
        if(this.cart[i].id === salItem.id){
          this.$store.commit('removeImg', i)
        }
      }   
    }, 
    toggleFavorite(id){
      this.$store.commit('favoriteStatus', id);      
    }   
  }  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.mainPic{
  margin: 20px;
  position: relative;
}
.priceIcon{
  display: flex;
  justify-content: space-between;
}
.price .del{
  font-size: 0.8rem;
  text-decoration:line-through
}
.price .red{
  color: red;
}
.favBtn{
  /*border-radius: 20px;*/
  border: none;
  background: none;
}

.isFavorite{
  color: #20AFEA ;
}

.cart, .trash{
  color: #20AFEA;
}
</style>
